body{
  background-color: #efefef;
  margin: 0;
  h2{
    color: #202020;
  }
}

.head{
  position: fixed;
  top: 0;
  left:0;
  width: 100%;
  background-color: #7fff7f;
  padding: 10px;
  img{
    display: inline-block;
    width: 30px;
    height: auto;
  }
  h1{
    display: inline-block;
    font-size: 2rem;
    margin: 0;
    margin-bottom: 5px;
    color: #202020;
  }
  .menu{
    margin-left: 25px;
    ol{
      margin: 0;
      padding: 0;
      li{
        display: inline;
        margin: 0 2px;
        a{
          text-decoration: none;
          color: #202020;
          padding: 3px 5px;
          position: relative;
        }
        a:hover::after{
          content: "";
          position: absolute;
          bottom: 0;
          left: 10%;
          height: 2px;
          width: 80%;
          background-color: #202020;
        }
      }
    }
  }
}

.timer{
  text-align: center;
  background-color: #ffffff;
  margin-top: 90px;
  padding: 15px;
  width: 100%;
  height: auto;
  .set_time{
    display: flex;
    justify-content: center;
    margin-bottom: 30px; 
    #week,#hour, #min{
      display:inline-flex;
      margin: 0 15px;
      select{
        color: #202020;
        font-size: 1.2rem;
        width: 120px;
        padding: 3px 0;
        text-align: center;
        border-radius: 10px;
        background-color: #f3f3f3;
      }
    }
    #change_time{
      input{
        color: #202020;
        font-size: 1rem;
        width: 80px;
        padding: 3px 0;
        border-radius: 15px;
        margin: 10px 0;
        background-color: #ffffff;
      }
    } 
  }
  .seted_timer{
    display: flex;
    justify-content: center;
    margin: 10px 0;
    p{
      color: #202020;
      font-size: 1.3em;
      margin: 3px 5px;
    }
  }
  #put_timer{
    input{
      padding: 5px 100px;
      font-size: 1rem;
      border-radius: 10px;
      background-color: #ffffff;
    }
  }
}

.check_time{
  text-align: center;
  margin-top: 90px;
  background-color: #ffffff;
  padding: 15px;
  height: auto;
  h2{
    margin: 5px;
    font-size: 1.5rem;
  }
  #button_box{
    margin: 10px 0;
    input{
      font-size: 1rem;
    }
  }
  #table{
    display: inline-block;
    table{
      border-collapse: collapse;
      th,td{
        border-bottom: 2px solid #707070;
        padding: 15px 25px;
        background-color: #fefefe;
      }
      th{
        background-color: #ccff99;
      }
      tr:nth-child(odd) td{
        background-color: #efefef;
      }
    }
  }
}

.home{
  margin-top: 90px;
  background-color: #ffffff;
  padding: 15px;
  height: auto;
}